<template>
  <div class="login-container d-flex jc-center ai-center">
    <div class="login-inner-container p-3">
      <div class="login-image">
        <!-- <h1>Chat</h1> -->
      </div>
      <van-tabs v-model="model.active" :border="false">
        <van-tab title="">
          <van-form @submit="login">
            <div class="field">
              <van-field
                v-model="model.login.phone"
                :rules="[{ validator: validatorPhone, message: '' }]"
                label=""
                placeholder=""
              />
              <van-field
                type="password"
                v-model="model.login.password"
                :rules="[{ required: true, message: '' }]"
                label=""
                placeholder=""
              />
            </div>
            <!-- <van-button type="info" block @click="login"></van-button> -->
            <van-button round block type="info" native-type="submit"></van-button>
            <!-- <p class="fs-md text-dark"></p> -->
            <p class="fs-md text-dark"></p>
          </van-form>
        </van-tab>
        <van-tab title="">
          <van-form @submit="register">
            <div class="field">
              <van-field
                v-model="model.register.username"
                :rules="[{ validator: validatorUsername, message: '1-8' }]"
                placeholder=""
                label=""
              />
              <van-field
                v-model="model.register.phone"
                placeholder=""
                label=""
                :rules="[{ validator: validatorPhone, message: '' }]"
              />
              <van-field
                type="password"
                v-model="model.register.password"
                placeholder=""
                label=""
                :rules="[{ validator: validatorPassword, message: '3' }]"
              />
              <van-field
                type="password"
                v-model="model.register.password2"
                placeholder=""
                label=""
                :rules="[{ validator: validatorPassword2, message: '' }]"
              />
            </div>
            <van-button round block type="info" native-type="submit"></van-button>
          </van-form>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      model: {
        active: 0,
        register: {
          username: "", //
          phone: "", // 
          password: "", // 
          password2: "" // 
        },
        login: {
          username: "",
          password: ""
        }
      }
    };
  },
  methods: {
    // 
    async login() {
      const res = await this.$http.post("/login", this.model.login);
      console.log("user", res);
      if (res.status === 200) {
        // sessionStorage.token = res.data.token;
        sessionStorage.setItem("token", res.data.token);
        //  store 
        sessionStorage.setItem("user", JSON.stringify(res.data.user));
        this.$store.commit("updateUser", res.data.user);
        //socket,storeSOCKET_connectsocket.iduserId
        this.$socket.disconnect()
        this.$socket.connect()
        this.$toast({
          type: "success",
          message: ""
        });
        this.$router.push("/main");
      } else {
        this.$toast({
          type: "fail",
          message: ""
        });
      }
    },
    // login() {
    //   this.$http.post("/login", this.model.login).then(resolved =>{
    //      sessionStorage.token = resolved.data.token;
    //   this.$toast({
    //     type: "success",
    //     message: ""
    //   });
    //   this.$router.push("/main");
    //   })
    // },
    // 
    async register() {
      const res = await this.$http.post("/register", this.model.register);
      this.$toast({
        type: "success",
        message: ""
      });
      console.log(res);
      this.model.login.phone = res.data.phone;
      this.model.active = 0;
    },
    // 
    validatorUsername(val) {
      return /^.{1,8}$/.test(val);
    },
    // 
    validatorPhone(val) {
      return /^1[3456789]\d{9}$/.test(val);
    },
    // 
    validatorPassword(val) {
      return /^.{3,30}$/.test(val);
    },
    // 
    validatorPassword2() {
      if (!(this.model.register.password === this.model.register.password2)) {
        this.model.register.password = "";
        this.model.register.password2 = "";
        return false;
      } else {
        return true;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/_variables.scss";
.login-container {
  padding-top: map-get($spacing-sizes, "5") * $spacing-base-size;
  padding-left: map-get($spacing-sizes, "2") * $spacing-base-size;
  padding-right: map-get($spacing-sizes, "2") * $spacing-base-size;
  .login-inner-container {
    // width: 90%;
    background-color: #fff;
    border-radius: 0.25rem;
    position: relative;

     .login-image{
       position: absolute;
       top: -1.7rem;
       left: 3rem;
       z-index: 2;
       color: #F596AA;
     }

    .field {
      padding-bottom: map-get($spacing-sizes, "1") * $spacing-base-size;
    }
    // .van-button--block {
    //   width: 99%;
    // }
  }
}
</style>